@media screen and (max-width: 640px) {
  #navElement {
    color: #000;
    background: #fff;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    width: 200px;
    height: 300px;
    display: flex;
    position: absolute;
    top: 70px;
    right: 0;
  }

  nav {
    flex-direction: row-reverse;
  }
}

.bar {
  width: 25px;
  height: 2px;
  margin: 3px 0;
}
