.home {
  background-image: url("https://getwallpapers.com/wallpaper/full/f/3/c/1425380-new-s7-edge-wallpapers-1080x1920-xiaomi.jpg");
  background-size: 100%;
  background-attachment: fixed;
}

img {
  max-width: 100%;
  height: auto;
}

@media screen and (min-width: 800px) {
  .home {
    background-image: url("https://wallpapercave.com/wp/7qOwD8U.jpg");
    background-repeat: no-repeat;
    background-size: stretch;
    height: 670px;
  }
}

.arow {
  text-shadow: 0 5px 10px #b1e0dd;
  animation: 2s infinite bounce;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }

  40% {
    transform: translateY(-20px);
  }

  60% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(0);
  }
}
